@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta&family=Nanum+Gothic&display=swap);
body,html {
  margin: 0;
  padding:0;
  font-family: 'Nanum Gothic', sans-serif;

  overflow-X:hidden;
  background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.nav, .contain{
    background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: rgb(255, 255, 255);
    font-size:1.1rem;
    height:3.4em;
    display:flex;
    align-items:center;
    justify-content: flex-end;
    z-index:1;
    transition:top 0.3s;
}

#lastLi{
    margin-right:4rem;
}

.nav > .active{
    color: #ffe989;
}

.nav span{
    padding:2rem;
    max-height:100%;
    cursor: pointer;
}

.sticky{
    position:fixed;
    width:100%;
    margin:0;
    top:-3.4em;
}

.icon{
    margin-right:auto;
    margin-left:13px;
}
.cp img{
    width:75px;
    height:45px;
}

@media only screen and (max-width:720px){
    .icon{
        display:none;
    }
    .cp{
        width:65px;
    }
}

@media only screen and (max-width:660px){
    .cp{
        display:none;
    }
    .nav span{
        cursor: auto;
        padding:1.5rem;
    }
    .nav{
        font-size:0.8rem;
        height:3rem; 
        justify-content: center;
    }
    #lastLi{
        margin:0;
    }
 }

 @media only screen and (max-width:350px){
    .cp{
        display:none;
    } 
    .nav{
         font-size:0.6rem;
         height:100%;
     } 
}
    
.view{
    display:flex;
    flex-direction: column;
    min-height: 100vh;
    height: 100vh;
    justify-content: center;
    color:white;
    padding: 0rem 5.4rem;
    margin-left:3rem;
    margin-bottom: 0;
    line-height: 4.5em;
    z-index: -1;
}

.view h1{
    font-size: 3.5rem;
    font-weight: 625;
    margin-bottom: 1rem;
    text-align: left;
}

.view h1 span{
    background-image: linear-gradient(135deg, #ef32d9 0%, #89fffd 100%);
    background-clip:text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.view p>a{
    border-style: none;
    background: transparent;
}

.hire{
    padding:0.4rem 1.2rem;
    background-image: linear-gradient(135deg, #ef32d9 0%, #89fffd 100%);
    border: 2px solid transparent;
    background-clip:text;
    font-size: 1.1rem;
    font-weight: bold;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-image: linear-gradient(135deg, #ef32d9 0%, #89fffd 100%);
    border-image-slice: 1;
    transition:border-width flat 50ms;
}

.hire:hover{
    cursor: pointer;
    -webkit-text-fill-color: #fff;
    border-width: 6px;
    background-clip: border-box;
    -webkit-background-clip: border-box;
    text-decoration:none;
    background-image:  linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
    border-image:  linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
}

@media only screen and (max-width:1100px){
    .view{
        justify-content: center;
        align-items: center;
        margin:0;
        padding: 0rem 1rem;
    }
    .view h1{
        font-size: 2.5em;
        text-align: center;
        font-weight: 640;
        margin-bottom: 0.5rem;  
    }
}

@media only screen and (max-width:600px){
       .view h1{
           font-size:2em;
       }
       .view a{
           padding:0.3rem 1rem;
           font-size:1.2rem;
           font-weight: 600;
       }
}

.about{
    color:white;
    display:flex;
    flex-direction:column;
    padding:2rem;
    width:100%;
    align-items:center;
    justify-content: center;
    background-image: linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
}

.about h1{
    color:white;
    margin-top:25px;
    text-align: center;
    font-size: 2.8rem;
    font-weight: 600;
    padding:0.8em 0 0 0;
}

.about li span{
    color:#0f0c29;
}

.sec{
    display:grid;
    grid-gap:50px;
    grid-template-columns: repeat(auto-fit,minmax(400px,1fr));
    align-items: center;
    margin-bottom:8rem;
}

.sec img{
    width:45%;
    height:330px;
    border-radius:15px;
    justify-self: center;
}

.desc{
    width:90%;
    color:#fff;
    justify-self: left;
    padding-bottom:50px
}

.desc li{
    color:whitesmoke;
    font-family:'Mukta',sans-serif;
    line-height:2rem;
    font-size:22px;
    letter-spacing: 1px;
    padding-bottom:11px;
}

.desc span{
    color:black;
}

.resume{
    padding:0.3rem 1rem;
    border: 2px solid transparent;
    background-clip:text;
    font-size: 1.2rem;
    font-weight: bold;
    -webkit-background-clip: text;
    -webkit-text-fill-color: white;
    text-decoration:none;
    background-image: linear-gradient(135deg, #ef32d9 0%, #89fffd 100%);
    border-image: linear-gradient(135deg, #fff,0%,#fff);
    border-image-slice: 1;
    transition:border-width ease-out 20ms;
}

.resume:hover{
    -webkit-text-fill-color:#5691c8;
    border-width: 3px;
    background-clip: border-box;
    -webkit-background-clip: border-box;
    text-decoration:none;
    background-image: linear-gradient(to right,rgb(255, 255, 255),white);
}

@media only screen and (max-width:1200px){
    .sec{
        grid-template-columns: repeat(auto-fill,minmax(390px,1fr));
    }
    .about{
        -webkit-clip-path: none;
                clip-path: none;
    }
    .about h1{
        font-size:2.5rem;
        font-weight:500;
    }
}

@media only screen and (max-width:950px){
    .desc{
        text-align:center;
        justify-self:center;
        font-size:0.8rem;
    }
    .about{
        -webkit-clip-path: none;
                clip-path: none;
        padding:0;
    }
    .about h1{
        font-size:2.2rem;
        font-weight:500;
    }
}

@media only screen and (max-width:620px){
    .sec img{
        width:55%;
    }
    .desc{
        width:85%;
    }
    .desc li{
        font-size:1.2rem;
    }
}
.button{
    padding:0.4rem 1rem;
    border: 2px solid transparent;
    background-clip:text;
    font-size: 1.2rem;
    font-weight: bold;
    -webkit-background-clip: text;
    -webkit-text-fill-color:transparent;
    text-decoration:none;
    border-image: linear-gradient(100deg, #ef32d9 0%, #89fffd 100%);
    background-image:linear-gradient(100deg, #ef32d9 0%, #89fffd 100%);
    border-image-slice: 1;
    transition:border-width ease-out 20ms;
}

.button:hover{
    -webkit-text-fill-color:white;
    background-clip: border-box;
    -webkit-background-clip: border-box;
    text-decoration:none;
    background-image: linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
}

@media only screen and (max-width:950px){
    .button{
        font-size:1.1rem ;
    }
}
.element{
    color:white;
    line-height:1.7em;
    padding-top:1.5rem;
}

.area{
    display:grid;
    margin-top:7.5rem;
    grid-gap:40px;
    grid-template-columns: repeat(auto-fit,minmax(360px,1fr));
}

.exp{
    width:60%;
    justify-self: center;
}

.exp span{
    display:flex;
    flex-flow:row wrap;
}

.exp h2{
    margin:0;
    font-size: 28px;
    padding-bottom:10px;
    font-weight: 600;
    color:#ffe989;
}

.exp li{
    font-family:'Mukta',sans-serif;
    line-height:2rem;
    font-size:21px;
    letter-spacing: 1px;
    padding-bottom:10px;
}

.Code{
    text-decoration:none;
    padding:0rem 1rem;
    background-image: linear-gradient(135deg, #ef32d9 0%, #89fffd 100%);
    border: none;
    background-clip:text;
    font-size: 1.2rem;
    font-weight: bold;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-image-slice: 1;
    cursor: pointer;
}

.Code:hover{
    margin:0 5px;
}

.area img,.wrapper{
    justify-self: left;
    align-self:center;
    max-height:400px;
    width:75%;
}

.area img{
    width:100%;
}

@media only screen and (max-width:950px){
    .exp{
        width:80%;
    }
    .exp li{
        font-size:19px;
    }
    .area img,.wrapper{
        justify-self: center;
        max-width:90%;
        margin:0 auto;
    }
    .Code{
        font-size:18px;
    }
    .area{
         margin-top:4rem;        
    }
}

.more{
    text-align: center;
    height:100px;
}
.tech {
  margin-top:30px;
}

.tech h3{
    font-family: Hind Siliguri,Helvetica,Arial,sans-serif;
    font-weight: lighter;
    text-align:center;
    color:#faea8f;
}

.stack{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}

.stack img{
    width:105px;
    height:100px;
    margin:40px;
}

@media only screen and (max-width:700px){
    .stack img{
        width:80px;
        height:80px;
        margin:30px;
    }
}

.done{
    padding:3rem 0;
}

.done p{
    text-align:center;
    padding:6rem;
    margin:1.5rem 0;
    color:rgb(179, 248, 231);
}

.form{
    padding-bottom:4rem;
    background-image: linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
    -webkit-clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(0 15vh, 100% 0, 100% 100%, 0 100%);
}

.form h1{
    color:white;
    padding-top: 8rem;
}

.container{
    display:flex;
    justify-content: center;
}

.inputs{
    background:white;
    color:#6e6d6d;
    min-width:33%;
    display:grid;
    grid-gap:28px;
    grid-template-rows: repeat(2,1fr) 2fr 1fr;
    padding:2.8rem 2.4rem;
    box-shadow:4px 4px 15px rgb(126, 125, 125);
}

input:focus ,textarea:focus{
    outline: none;
    color:rgb(54, 54, 54);
}

input,textarea{
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style:groove;
  padding:6px;
  background-color: #eee;
  border-color: rgb(245, 67, 201);
  font-size: 1.1em;
  font-family: 'Mukta', sans-serif;
  font-weight: 600;
  resize:none;
}

.btn{
    justify-self: left;
    text-decoration:none;
    padding:0.4rem 0.7rem;
    background-image: linear-gradient(135deg, #fc00ff 0%, #5691c8 100%);
    border: 1px solid blue;
    background-clip:text;
    font-size: 1.1rem;
    font-weight: bold;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-image-slice: 1;
}

.btn:hover{
    background-clip:border-box;
    -webkit-background-clip: border-box;
    -webkit-text-fill-color: white;
    transform: scale(1.05);
    cursor: pointer;
}

.sending{
    width:100%;
    margin:0;
    font-size:17px;
    color:rgb(38, 141, 13);
}

.not-sending{
    width:0;
    height:0;
    display:none;
}

@media only screen and (max-width:950px){
       .form{
           -webkit-clip-path: none;
                   clip-path: none;
       }
       .form h1{
           padding-top: 3rem;
       }
       .inputs{
           min-width: 60%;
       }
       input,textarea{
           font-size:1.1rem;
       }
       .btn{
        padding:0.3rem 0.6rem;
        font-size: 1rem;
       }
}

@media only screen and (max-width:650px){
    .done p{
        font-size: 1.2rem;
        font-weight: 500;
    }
}
.foot{
    padding:3rem 3rem 0 3rem;
    display:flex;
    flex-direction: column;
    background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.foot span{
    display:flex;
    justify-content: center;
}

.foot .profile{
    padding:20px;
}

.foot .profile img{
    border-radius:80%;
    background: white;
    
}

.foot .profile img:hover, .codechef:hover{
    margin-top: -5px;
}

.hr{
    border:0.5px solid rgb(161, 159, 159);
    width:25%;
}

.foot p{
    text-align: center;
    color:rgb(224, 219, 219);
    font-size: medium;
}

@media only screen and (max-width:540px){
    .codechef,.profile{
        cursor:none;
    }
}

.loader-wrapper{
    background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-filter:blur(15);
            filter:blur(15);
    width:100%;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

.loader {
    background: #302b63 ;  /* fallback for old browsers */
    border-top: 12px solid #302b63;
    border-bottom: 12px solid #302b63;
    border-right: 12px solid #d1ac07;
    border-left: 12px solid #d1ac07;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
  }
  
@-webkit-keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.head{
    color:white;
    width:100%;
    padding-top:5.5rem;
    text-align: center;
    font-size: 2.4rem;
    font-weight: 600;
}

.line{
    margin-top:-12px;
    border:3px dotted #89fffd;
    border-radius:50%;
    max-width:250px;
}

p{
    font-family:'Mukta',sans-serif;
      line-height:2rem;
      font-size:22px;
      letter-spacing: 1px;
}

@media only screen and (max-width:950px){
    .head{
        font-size:2.2rem;
        font-weight:475;
    }
}

@media only screen and (max-width:600px){
    p{
        font-size:1.4rem;
    }
}

